<template>
  <div class="cart__block">
    <ul class="cart__orders">
      <li class="cart__order" v-for="product in OrderInfo" :key="product.id">
        <h3>{{product.product.title}}</h3>
        <b>{{product.price | numberFormat }}₽ x {{product.quantity}}</b>
        <span>Артикул: {{product.id}}</span>
      </li>
    </ul>
    
    <div class="cart__total">
      <p>Доставка: <b>500 ₽</b></p>
      <p>Итого: <b>{{productCount}}</b> товара на сумму <b>{{productPrice | numberFormat}} ₽</b></p>
    </div>

    <button class="cart__button button button--primery" type="submit" v-if="isButtonShown">
      Оформить заказ
    </button>
  </div>

  
</template>

<script>
import { mapGetters } from 'vuex';
import numberFormat from '../helpers/numberFormat';

export default {
  filters: {
    numberFormat,
  },
  computed: {
    ...mapGetters({
      totalPrice: 'cartTotalPrice',
      totalProductCount: 'cartTotalProductCount',
      orderCount: 'orderTotalCount'
    }),
    OrderInfo() {
      return this.$store.state.orderInfo
      ? this.$store.state.orderInfo.basket.items
      : this.$store.state.cartProductsData
    },
    productCount() {
      return this.$store.state.orderInfo
      ? this.orderCount
      : this.totalProductCount;
    },
    productPrice() {
      return this.$store.state.orderInfo
      ? this.$store.state.orderInfo.totalPrice
      : this.totalPrice
    },
    isButtonShown() {
      return !this.$store.state.orderInfo
    }
  },

}
</script>

